/* ///////////////  ABOUT COMPONENT ////////////////// */

.about-cont{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    width: 100vw;
    height: 100vh;
}
.h1-about{
    position: relative;
    top: 5%;
    color: rgba(255, 255, 255, 0.753);
    font-size: 2rem;
}
.h2-description{
    position: absolute;
    top: 15%;
    right: 30%;
    font-size: 2rem;
}
.p-description{
    width: 30vw;
    text-align: center;
    line-height: 4vh;
    font-size: 1.5rem;
}

.about-description{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    color: white;
    width: 50vw;
    height: 100vh;
}
.about-subcont{
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
}
.p-changue{
    transition: .2s;
    position: absolute;
    bottom: 15%;
    font-size: 1rem;
    cursor: pointer;
    z-index: 8;
}
.p-changue:hover{
    transition: .2s;
    color: rgb(162, 221, 255);
    font-size: 1.1rem;
}
.foto-perfil{
    position: relative;
    background-image: url(/portafolio/static/media/fotoperfil.d45cd8ea.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 120%;
    border-radius: 50%;
    opacity: .8;
    z-index: 5;
    border: 2px solid rgba(255, 255, 255, 0.534);
    width: 25vw;
    height: 50vh;
    -webkit-animation: spawn 1s ease-in-out;
            animation: spawn 1s ease-in-out;
}
.foto-perfil::before{
    transition: .3s;
    content: 'Soy Yo!';
    position: relative;
    top: 0%;
    left: 0%;
    color: white;
    font-size: 0%;
    -webkit-transform: scale(0);
            transform: scale(0);

}
.foto-perfil:hover::before{
    transition: .3s;
    top: -10%;
    font-size: 150%;
}
@-webkit-keyframes spawn {
    from{opacity: 0;} to{opacity: .8;}
}
@keyframes spawn {
    from{opacity: 0;} to{opacity: .8;}
}
.none{
    display: none;
}
